<script>
import ChatMessagesStatusLine from './ChatMessagesStatusLine.svelte';
import Icon from './Icon.svelte';
import Link from './Link.svelte';
import Time from '../js/Time';
import {ensureChildNode} from '../js/util';
import {getContext} from 'svelte';
import {gotoUrl, urlFor} from '../store/router';
import {l, lmd, topicOrStatus} from '../js/i18n';
import {showEl} from '../js/util';

export let connection;
export let dialog;
export let input;

const user = getContext('user');
const events = user.events;

let messages = [];
let unreadFrom = 0;

$: calculateMessages(dialog, $events);

function calculateMessages(dialog, $events) {
  const extraMessages = [];

  if (dialog.messages.length == 0 && dialog.dialog_id && dialog.dialog_id != 'notifications') {
    extraMessages.push(convosMessage({
      message: 'There are %1 [users](%2) in this conversation.',
      type: 'notice',
      vars: [dialog.participants.length, urlFor(dialog.path + '#activeMenu:settings')],
    }));
    extraMessages.push(convosMessage({
      message: 'Start chatting by writing a message in the input field, or click on the conversation name ([%1](%2)) to see further details.',
      type: 'notice',
      vars: [dialog.name, urlFor(dialog.path + '#activeMenu:settings')],
    }));
  }

  if (connection.frozen) {
    if (!connection.is('unreachable')) {
      extraMessages.push(convosMessage({
        message: 'Disconnected. Your connection %1 can be edited in [settings](%2).',
        vars: [connection.name, urlFor(connection.path + '#activeMenu:settings')],
      }));
    }
  }
  else if (dialog.frozen && !dialog.is('locked')) {
    extraMessages.push(convosMessage({message: topicOrStatus(connection, dialog).replace(/\.$/, ''), vars: []}));
  }

  if ($events.wantNotifications === null) {
    extraMessages.push(convosMessage({
      message: 'Do you want notifications when someone sends you a private message? [Yes](%1) / [No](%2)',
      vars: ['#call:events:requestPermissionToNotify', '#call:events:rejectNotifications'],
    }));
  }

  messages = extraMessages.length ? dialog.messages.concat(extraMessages) : dialog.messages;
  unreadFrom = dialog.unread + extraMessages.length;
}

function convosMessage(message) {
  return {
    color: 'inherit',
    from: 'Convos',
    fromId: 'Convos',
    markdown: lmd(message.message, ...message.vars),
    ts: new Time(),
    type: 'error',
    ...message,
  };
}

function dayChanged(i) {
  return i == 0 ? false : messages[i].ts.getDate() != messages[i - 1].ts.getDate();
}

function gotoDialogFromNotifications(e) {
  if (dialog.connection_id) return;
  const target = e.target.closest('.message');
  const message = messages[target.dataset.index];
  if (!message) return;
  e.preventDefault();
  gotoUrl(notififactionUrl(message));
}

function isSameSender(i) {
  return i == 0 ? false : messages[i].fromId == messages[i - 1].fromId;
}

function notififactionUrl(message) {
  const url = ['', 'chat', message.connection_id, message.dialog_id].map(encodeURIComponent).join('/');
  return url + '#' + message.ts.toISOString();
}

function senderIsOnline(message) {
  return message.fromId == 'Convos' || message.fromId == dialog.connection_id || !dialog.dialog_id || dialog.findParticipant(message.fromId);
}

function toggleDetails(e) {
  const messageEl = e.target.closest('.message');
  user.embedMaker.toggleDetails(messageEl, messages[messageEl.dataset.index]);
}
</script>

{#if dialog.messages.length == 0}
  <h2>{l(dialog.dialog_id == 'notifications' ? 'No notifications.' : 'Welcome!')}</h2>
{/if}

{#if messages.length > 40 && dialog.is('loading')}
  <ChatMessagesStatusLine class="for-loading" icon="spinner" animation="spin">{l('Loading...')}</ChatMessagesStatusLine>
{/if}

{#each messages as message, i}
  {#if i && i == messages.length - unreadFrom}
    <ChatMessagesStatusLine class="for-last-read" icon="comments">{l('New messages')}</ChatMessagesStatusLine>
  {/if}

  {#if message.endOfHistory}
    <ChatMessagesStatusLine class="for-start-of-history" icon="calendar-alt">{l('Started chatting on %1', message.ts.getHumanDate())}</ChatMessagesStatusLine>
  {:else if dayChanged(i)}
    <ChatMessagesStatusLine class="for-day-changed" icon="calendar-alt">{message.ts.getHumanDate()}</ChatMessagesStatusLine>
  {/if}

  <div class="message is-type-{message.type}"
    class:is-not-present="{!senderIsOnline(message)}"
    class:is-sent-by-you="{message.from == connection.nick}"
    class:is-highlighted="{message.highlight}"
    class:has-not-same-from="{!isSameSender(i) && !dayChanged(i)}"
    class:has-same-from="{isSameSender(i) && !dayChanged(i)}"
    on:click="{gotoDialogFromNotifications}"
    data-index="{i}">

    <Icon name="pick:{message.fromId}" color="{message.color}"/>
    <b class="message__ts" aria-labelledby="{message.id + '_ts'}">{message.ts.getHM()}</b>
    <div role="tooltip" id="{message.id + '_ts'}">{message.ts.toLocaleString()}</div>
    {#if dialog.connection_id}
      <a href="#input:{message.from}" on:click|preventDefault="{() => input.add(message.from)}" class="message__from" style="color:{message.color}" tabindex="-1">{message.from}</a>
    {:else}
      <a href="{notififactionUrl(message)}" class="message__from" style="color:{message.color}">{l('%1 in %2', message.from, message.dialog_id)}</a>
    {/if}
    <div class="message__text">
      {#if message.type == 'error' || message.type == 'notice'}
        <Icon name="{message.type == 'error' ? 'exclamation-circle' : 'info-circle'}" on:click="{toggleDetails}"/>
      {/if}
      {@html message.markdown}
    </div>
  </div>
{/each}

{#if (connection.is && connection.is('unreachable')) || !dialog.is('success')}
  <ChatMessagesStatusLine class="for-loading" icon="spinner" animation="spin"><a href="{urlFor('/')}">{l('Loading...')}</a></ChatMessagesStatusLine>
{/if}
